import '../styles/tokens-grid.css'

interface ITokensGridProps {
  tokens: Record<string, string>
  hasRemValue?: boolean
  hasGraphView?: boolean
}

export function TokensGrid({
  tokens,
  hasRemValue = false,
  hasGraphView = false,
}: ITokensGridProps) {
  return (
    <table className="tokens-grid">
      <thead>
        <tr>
          <th>Name</th>
          <th>Value</th>
          {hasRemValue && <th>Pixels</th>}
          {hasGraphView && <th></th>}
        </tr>
      </thead>
      <tbody>
        {Object.entries(tokens).map(([key, value]) => {
          return (
            <tr key={key}>
              <td>{key}</td>
              <td>{value}</td>
              {hasRemValue && (
                <td>{Number(value.replace('rem', '')) * 16} px</td>
              )}
              {hasGraphView && (
                <td>
                  <div
                    style={{
                      height: '1rem',
                      width: value,
                      backgroundColor: '#FFF',
                    }}
                  ></div>
                </td>
              )}
            </tr>
          )
        })}
      </tbody>
    </table>
  )
}
